.checkbox__input {
  position: absolute;
  top: -9999px;
  left: -9999px;
  opacity: 0;
  visibility: hidden;
}

.checkbox__input:disabled ~ .checkbox__text {
  color: #999;
  pointer-events: none;
}

.checkbox__input:disabled ~ .checkbox__text:before {
  border-color: #999;
}

.checkbox__input:checked ~ .checkbox__text:before {
  background: #0E0E0E url(../img/icon/check.svg) no-repeat 50% 50%;
  background-size: 18px 18px;
  border-color: #0E0E0E;
