}

.header__controll-hide {
  position: absolute;
  right: -46px;
  top: calc(100% + 9px);
  border-radius: 12px;
  background: #FFF;
  box-shadow: 0px 8px 10px 0px rgba(175, 175, 175, 0.2), 0px 6px 30px 0px rgba(175, 175, 175, 0.12), 0px 16px 24px 0px rgba(175, 175, 175, 0.14);
  opacity: 0;
  visibility: hidden;
  transform: translateY(0);
  padding: 10px 8px 0 0;
  transition: opacity 0.2s ease 0.3s, visibility 0.2s ease 0.3s, transform 0.2s ease 0.3s;
}

.header__controll-hide_active {
  opacity: 1;
  visibility: visible;
  transform: translateY(12px);
}

.header__controll-hide--small {
  padding: 0;
}

.header__controll-sum {
  position: absolute;
  top: 0;
  right: 10px;
