/* Price Range */

price-range {
  --thumb-size: 20px;
  --thumb-color: #0E0E0E;
  --thumb-shadow-size: 5px;
  --track-size: 1px;
  --track-color: #E1E1E3;
  --track-highlight-color: #999;
  width: 100%;
  display: block;
}

/* Price Range Containers */

price-range > .range-container {
  position: relative;
  width: 100%;
  height: calc(var(--thumb-size) + 1px);
}

price-range > .range-container > div {
  width: 100%;
  height: var(--track-size);
  background: var(--track-color);
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
}

/* Price Range Input */

price-range input[type=range] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  width: 100%;
  margin: 0;
  padding: 0;
  background: transparent;
  border: none;
  outline: none;
  pointer-events: none;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
}

/* Price Range Input Track */

price-range input[type=range]::-webkit-slider-runnable-track {
  -webkit-appearance: none;
  height: 5px;
}

price-range input[type=range]::-moz-range-track {
  -moz-appearance: none;
  height: 5px;
}

price-range input[type=range]::-ms-track {
  appearance: none;
  height: 5px;
}

/* Price Range Input Thumb */

price-range input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  margin-top: -7px;
  width: var(--thumb-size);
  height: var(--thumb-size);
  background: var(--thumb-color);
  border: none;
  border-radius: 50%;
  pointer-events: auto;
  cursor: pointer;
  box-shadow: 0 0 0 0 var(--thumb-color);
  transition: background-color 0.15s ease;
}

price-range input[type=range]::-webkit-slider-thumb:hover {
  background: #7D7D7D;
}

price-range input[type=range]::-moz-range-thumb {
  -moz-appearance: none;
  width: var(--thumb-size);
  height: var(--thumb-size);
  background: var(--thumb-color);
  border: none;
  border-radius: 50%;
  pointer-events: auto;
  cursor: pointer;
  box-shadow: 0 0 0 0 var(--thumb-color);
  transition: background-color 0.15s ease;
}

price-range input[type=range]::-moz-range-thumb:hover {
  background: #3A3A3C;
}

price-range input[type=range]::-ms-thumb {
  appearance: none;
  width: var(--thumb-size);
  height: var(--thumb-size);
  background: var(--thumb-color);
  border: none;
  border-radius: 50%;
  pointer-events: auto;
  cursor: pointer;
  box-shadow: 0 0 0 0 var(--thumb-color);
  transition: background-color 0.15s ease;
}

price-range input[type=range]::-ms-thumb:hover {
  background: #3A3A3C;
}

